.hoverd-menu {
  // opacity: 0;
  // visibility: hidden;
  position: absolute;
  top: 134px;
  left: 0;
  width: 100%;
  z-index: 8;
  background-color: rgb(250, 250, 250);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  // transition: all .5s ease-in-out;
  li {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 40px 20px;
    &:hover {
      .img-container {
        &::after {
          border-color: rgba(205, 219, 9, 1.0);
        }
      }
      .title {
        color: rgb(150, 179, 9);
      }
    }
  }
  .img-container {
    position: relative;
    width: 167px;
    height: 167px;
    border-radius: 50%;
    transition: all .4s ease-in-out;
    &::after {
      content: "";
      width: 177px;
      height: 177px;
      position: absolute;
      top: -5px;
      left: -5px;
      z-index: -1;
      border-style: solid;
      border-width: 1px;
      border-color: rgba(205, 219, 9, 0.0);
      border-radius: 50%;
      transition: all .4s ease-in-out;
    }
  }
  .title {
    font-family: ar-reg;
    font-size: 16px;
    color: #000;
    margin: 18px 0 0 0;
    transition: all .3s ease-in-out;
  }
}
