/*------ header start ------*/
.header {
  margin: 0px 0 0 0;
  .header-back {
    position: relative;
    overflow: hidden;
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        transform: scale(1.02);
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      .owl-prev, .owl-next {
        outline: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 30.348px;
        color: rgba(255, 255, 255, 0.702);
        transition: all .5s ease-in-out;
        &:hover {
          color: #000;
        }
      }
      .owl-prev {
        right: 40px;
      }
      .owl-next {
        left: 40px;
      }
    }
    .owl-dots {
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: 30px;
      transform: translateX(-50%);
      .owl-dot {
        width: 9px;
        height: 9px;
        background-color: rgba(0, 0, 0, 0.502);
        border-radius: 50%;
        outline: none;
        margin: 0 2.5px;
        &.active {
          background-color: rgb(141, 173, 9);
        }
      }
    }
  }
}
/*------ header end ------*/



/*------ category start ------*/
