/*--- card start ---*/
.card {
  position: relative;
  width: 220px;
  background-color: rgb(255, 255, 255);
  text-align: center;
  transition: all .5s ease-in-out;
  .card-img {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    img {
      transition: all .5s ease-in-out;
    }
    &:hover {
      img {
        // transform: scale(0.940);
      }
    }
  }
  .card-body {
    margin: 0 0 0 0;
    padding: 0 18px;
    .card-title {
      font-family: ar-reg;
      font-size: 15px;
      color: rgb(0, 0, 0);
      min-height: 55px;
      display: block;
    }
    .card-price {
      padding: 0 0 20px 0;
      .new {
        font-family: ar-bold;
        font-size: 20px;
        color: #db2b1e;
      }
      .old {
        font-family: ar-md;
        font-size: 16px;
        color: #7d7d7d;
        text-decoration: line-through;
      }
    }
  }
  .btns {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .5s ease-in-out;
  }
  .add-to-card {
    background-image: linear-gradient( 0deg, rgb(90,137,9) 0%, rgb(141,173,9) 100%);
    width: 156px;
    height: 32px;
    outline: none;
    border: none;
    padding: 0;
    border-radius: 50px;
    color: rgb(255, 255, 255);
    font-family: ar-md;
    font-size: 14px;
    transition: all .5s ease-in-out;
    span {
      font-size: 16px;
      margin: 0 0 0 5px;
      
    }
    &:hover {
      background-image: linear-gradient( 90deg, rgb(90,137,9) 0%, rgb(141,173,9) 100%);
    }
  }
  .add-to-fav {
    border: none;
    outline: none;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: rgb(189, 189, 189);
    font-size: 16px;
    color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px 0 0;
    transition: all .5s ease-in-out;
    &:hover {
      background-image: linear-gradient( 0deg, rgb(90,137,9) 0%, rgb(141,173,9) 100%);
    }
  }
  &:hover {
    margin: -10px 0 0 0;
    .btns {
      top: 40%;
      opacity: 1;
      visibility: visible;
    }
  }
}
/*--- card end ---*/



